	module.exports = {"data":{"markdownRemark":{"html":"<p><em>This post is a WIP</em></p>\n<p><img src=\"https://i.imgur.com/LE7G4bH.jpg\" alt=\"flask file app\"></p>\n<p>File-hosting. </p>\n<p>From an entrepreneurial perspective, creating a file hosting application is a probably a complete waste of time. With <em>Google Drive</em> and <em>Dropbox,</em> who are able to offer gigs of storage for free, competition in this space seems quite tough.</p>\n<p>But I'm not an entrepreneur, I'm a developer. As such, I created a simple file hosting application using Flask, and I am glad I did.</p>\n<h2>The App</h2>\n<hr>\n<p>Once users authenticate their account, they may upload files with the following extensions:</p>\n<table>\n<thead>\n<tr>\n<th align=\"center\">Office</th>\n<th align=\"center\">Images</th>\n<th align=\"center\">Other</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"center\">docx</td>\n<td align=\"center\">jpg</td>\n<td align=\"center\">pdf</td>\n</tr>\n<tr>\n<td align=\"center\">xlsx</td>\n<td align=\"center\">png</td>\n<td align=\"center\">txt</td>\n</tr>\n<tr>\n<td align=\"center\">ppt</td>\n<td align=\"center\">gif</td>\n<td align=\"center\"></td>\n</tr>\n</tbody>\n</table>\n<p>These file extensions reflect what I would typically keep on Google drive, Dropbox, or a USB drive. As we will see, modifying what files the user can and can't upload is extremely simple, so this list is subject to change.</p>\n<h2>How it works</h2>\n<hr>\n<h2>Schemas</h2>\n<hr>","frontmatter":{"path":"/11-21-18","title":"My Dropbox Clone","tags":["Flask","Python","os library"]}}},"pathContext":{}}

